<template>
  <div class="container">
    <v-card>
      <v-btn v-on:click="updateFile">
        <v-icon>mdi-refresh</v-icon>
      </v-btn>
      <v-list>
        <v-list-item v-for="row in textListe" :key="row">
          <v-list-item-content>
            <div>
            <v-list-item-title v-text="row"></v-list-item-title>
            <v-btn color="red" v-on:click="delFile(row)">
              <v-icon>mdi-delete</v-icon>
            </v-btn>
            </div>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      
    </v-card>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      textListe: null,
    };
  },
  async mounted() {
    this.textListe = (
      await this.$axios({
        method: "get",
        url: "api/textListe",
      })
    ).data;
    console.log(this.textListe);
  },
  methods: {
    delFile: function (file) {
      this.$axios({
        method: "delete",
        url: "api/delete-file/" + file,
      });
    },
    updateFile: async function () {
      this.textListe = (
      await this.$axios({
        method: "get",
        url: "api/textListe",
      })
    ).data;
    }
  },
};
</script>

<style scoped>
.container {
  
  
}
</style>