<template>
  <v-card width="500" class="mx-auto mt-10">
    <v-card-title>
      <h1 class="display-1">Mot de passe Admin</h1>
    </v-card-title>
    <v-card-text>
      <v-form>
        <v-text-field
          label="Mot de passe"
          prepend-icon="mdi-account-cog"
          v-model="mdp"
        />
      </v-form>
    </v-card-text>

    <v-card-actions>
      <v-btn color="success" v-on:click="testMdp"
        >Confirmer</v-btn
      >
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  data: function () {
    return {
      mdp: "",
      adminMDP: "baboune",
    };
  },
  methods: {
    testMdp: function () {
      if (this.mdp === this.adminMDP) {
        this.$emit('accessValidation')
      }
    }
  }
};
</script>

