<template>
  <div class="container">
    <AdminAccess
      v-if="!accessAdmin"
      :access="accessAdmin"
      @accessValidation="accessValidation"
    ></AdminAccess>
    <div v-else>
      <TextsListe></TextsListe>
      <MultipleFile class="stayOnTop"></MultipleFile>
    </div>
  </div>
</template>


<script>
import AdminAccess from "../components/AdminAccess";
import TextsListe from "../components/TextsListe";
import MultipleFile from "../components/MultipleFile.vue";

export default {
  name: "Admin",
  components: {
    AdminAccess,
    TextsListe,
    MultipleFile,
  },
  data: function () {
    return {
      accessAdmin: false,
    };
  },
  methods: {
    accessValidation: function () {
      this.accessAdmin = true;
    },
  },
};
</script>


<style scoped>
.container {
  text-align: center;
}
.stayOnTop {
  position: absolute;
}
</style>